import React, { useState, useContext, useEffect } from 'react'
import { Spin, Typography, Input, Table, Tag, Button, message, notification, Icon } from 'antd'
import MonitorContext from '../../../../../../context/monitor/monitorContext'
import { Link, NavLink, } from 'react-router-dom'
import { InfoCircleOutlined } from '@ant-design/icons';
import moment from 'moment'

import '../Trigger/Trigger.scss'



const Trigger = (props) => {
    //useContext
    const monitorContext = useContext(MonitorContext)
    const {changeFlag, clearDoneList, flag=false,clearId, timeData = '', alarm = '', loading, currentId, doneList = [], doneListNum = 0, getDoneList } = monitorContext
    let _id; //idredux
    if (currentId) {
        _id = Number(currentId)
    } else {
        _id = props.location.query ? Number(props.location.query.id) : ''
    }

    useEffect(() => {  //useEffect didMount
        _id && getDoneList(_id)
        return handleComponentWillUnmount
        // eslint-disable-next-line
    }, [])

    const handleComponentWillUnmount = () => {
        // clearId()
        clearDoneList()
    }
    const close = () => {
        console.log(
            'Notification was closed. Either the close button was clicked or duration time elapsed.',
        );
    };
    const openNotification_empty = () => {
        const key = `open${Date.now()}`;
        const btn = (
            <Button type="primary" size="small" onClick={() => notification.close(key)}>
                
          </Button>
        );
        notification.open({
            message: '',
            description:
                '',
            btn,
            key,
            onClose: close,
        });
    };
    const openNotification = () => {
        const key = `open${Date.now()}`;
        const btn = (
            <Button type="primary" size="small" onClick={() => notification.close(key)}>
                
          </Button>
        );
        notification.open({
            message: '',
            description:
                `:
                 :${alarm.event_type}:${alarm.trigger},:${alarm.log}`,
            btn,
            key,
            onClose: close,
        });
    };
    const handleAlarmClick = () => {
        if (timeData && (timeData / 1000) < 300) {//
            openNotification()
            //30
        }
        else{
            openNotification_empty()
        }
    }
    if (alarm) { console.log(alarm)
    if(flag===false && doneList.length>0){
        console.log(flag,doneList,'ttt!')
            changeFlag()
        console.log(flag,doneList,'ttt!')
            handleAlarmClick()
        }

    }
  

  

  

  
    // console.log('alarm',moment(alarm.date).valueOf())
    // console.log(Number(1590554991748-1588840489993)/1000/3600/24)

    const { Text } = Typography
    const { Search } = Input

    //
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `${doneListNum}`,
        pageSize: 4,
        current: currentPage,
        total: doneListNum,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
    }




    const columns = [
        {
            title: '',
            dataIndex: 'type',
            key: 'type',
        },
        {
            title: '',
            dataIndex: 'level',
            key: 'level',
            render: text => <span style={{ width: '100%', backgroundColor: 'FFFF00' }}>{text}</span>,

        },
        {
            title: '',
            dataIndex: 'info',
            key: 'info',
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
        },

    ];

    const data = []; // antd

    if (doneList.length > 0) {
        doneList.forEach((item, index) => {
            data.push({
                key: String(index + 1),
                type: item.event_type ? item.event_type : '',
                level: item.trigger ? item.trigger : '',
                info: item.log ? item.log : '',
                date: item.date ? moment(item.date).format('YYYY-MM-DD HH:mm:ss') : '',

            })

        })

    }

   

    // const debounce=(func, wait=500) =>{
    //     let timeout;  // 
    //     return function(event){
    //         clearTimeout(timeout);  // ,
    //         event.persist && event.persist()   //
    //         //const event = e && {...e}   //
    //         timeout = setTimeout(()=>{
    //             func(event)
    //         }, wait);  //  xx ms  handler
    //     };
    // }

    //    debounce( handleAlarmClick);

   
   
   

   
 


   

    if (loading) {
        return <Spin className="myLoading" tip="..." size='large'>
            <div className='Trigger'>
                <div className='Trigger-table' style={{ marginBottom: '10px' }}>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>
            </div>
        </Spin>
    }
    return (
        <div className='Trigger'>
            <div className='Trigger-table' style={{ marginBottom: '10px', height: '100%' }}>
                {/* <h6 onClick={handleAlarmClick} className='alarm-tab'><InfoCircleOutlined />&nbsp;</h6> */}
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>
        </div>
    )
}

export default React.memo(Trigger)




